<template>
  <page
    @on-ready="onReady"
    ref="page"
    class="order-page create-action-page"
    :headerHeightVW="headerHeightVW"
    scrollbar
    :pullDownRefresh="true"
    :pullUpLoad="true"
    @on-refresh="onRefresh"
    @on-show="onShow"
    @on-load="onLoad"
  >
    <div slot="header">
      <van-nav-bar id="header" title="智能维修">
        <!-- <van-icon name="ellipsis" slot="right" @click.native="onMoreAction" /> -->
        <i class="icon-wode nav-icon-right" slot="right" @click="handleToMe"></i>
      </van-nav-bar>

      <van-popup v-model="moreAction" class="header-more" :style="{top: moreTop + 'vw'}">
        <div class="more-action">
          <van-cell title="提报故障" clickable @click.native="handleCreateFault" />
          <van-cell title="提报问题" clickable @click.native="handleCreateProblem" />
        </div>
      </van-popup>

      <div class="order-tabs">
        <van-tabs v-model="tabActive">
          <van-tab>
            <div slot="title">全部</div>
          </van-tab>
          <van-tab>
            <div slot="title">
              待接报
              <i class="order-tab-tag" v-if="receiveNum">{{receiveNum}}</i>
            </div>
          </van-tab>
          <van-tab>
            <div slot="title">
              执行中
              <i class="order-tab-tag" v-if="executingNum">{{executingNum}}</i>
            </div>
          </van-tab>
          <van-tab>
            <div slot="title">
              待审核
              <i class="order-tab-tag" v-if="reviewNum">{{reviewNum}}</i>
            </div>
          </van-tab>
          <van-tab>
            <div slot="title">
              已挂起
              <i class="order-tab-tag" v-if="suspendedNum">{{suspendedNum}}</i>
            </div>
          </van-tab>
          <van-tab>
            <div slot="title">已关闭</div>
          </van-tab>
        </van-tabs>
      </div>

      <div class="search-bar">
        <van-search placeholder="请输入搜索工单编号" v-model.lazy="filter.key" />

        <div class="search-action">
          <i class="search-scan search-icon" @click="handleScan"></i>
          <i class="search-filter search-icon" @click="handleFilter" v-if="tabActive==0"></i>
        </div>
      </div>
    </div>

    <van-loading type="spinner" v-if="loading" class="page-loading" />

    <div v-if="!orderList.length" style="text-align: center;line-height: 100px;">暂无数据</div>

    <transition-group class="order-list" tag="div" name="list">
      <item
        @click.native="handleDetail(item)"
        v-for="(item) in orderList"
        :key="item.id"
        :data="item"
        v-if="orderList.length"
      ></item>
    </transition-group>

    <template slot="bar">
      <van-popup
        v-model="filterShow"
        position="right"
        class="order-filter-popup"
        :lock-scroll="false"
      >
        <a-scroll scrollbar ref="filterScroller" v-if="filterShow" class="order-filter-scroll">
          <div class="order-filter-content">
            <van-cell-group title="工单类型" :border="false">
              <van-row gutter="10" class="filter-options">
                <van-col span="8">
                  <div
                    class="filter-options-item"
                    :class="{'is-active': tempFilter.type.indexOf('1')>=0}"
                    @click="handleToggleType('1')"
                  >故障修</div>
                </van-col>
                <van-col span="8">
                  <div
                    class="filter-options-item"
                    :class="{'is-active': tempFilter.type.indexOf('2')>=0}"
                    @click="handleToggleType('2')"
                  >计划修</div>
                </van-col>
              </van-row>
            </van-cell-group>
            <van-cell-group title="线别站点" :border="false">
              <van-button type="default" class="xbzd" @click="handleSelectLine">
                {{ tempFilter.lineObject && tempFilter.lineObject.name }} / {{ tempFilter.stationObject && tempFilter.stationObject.name }}
              </van-button>
            </van-cell-group>
            <van-cell-group title="所属专业" :border="false">
              <van-row gutter="10" class="filter-options">
                <van-col span="8">
                  <div
                    class="filter-options-item"
                    :class="{'is-active': tempFilter.profession==''}"
                    @click="handleToggleProfession('')"
                  >全部</div>
                </van-col>
                <van-col span="8" v-for="p in professionOptions" :key="p.id">
                  <div
                    class="filter-options-item"
                    :class="{'is-active': tempFilter.profession==p.id}"
                    @click="handleToggleProfession(p.id)"
                  >{{p.name}}</div>
                </van-col>
              </van-row>
            </van-cell-group>
            <van-cell-group title="故障等级" :border="false">
              <van-row gutter="10" class="filter-options">
                <van-col span="8">
                  <div
                    class="filter-options-item"
                    :class="{'is-active': tempFilter.level==''}"
                    @click="handleToggleLevel('')"
                  >全部</div>
                </van-col>
                <van-col span="8" v-for="l in levelOptions.filter(item => item.profession.includes(tempFilter.profession))" :key="l.id">
                  <div
                    class="filter-options-item"
                    :class="{'is-active': tempFilter.level==l.id}"
                    @click="handleToggleLevel(l.id)"
                  >{{l.name}}</div>
                </van-col>
              </van-row>
            </van-cell-group>
            <van-cell-group title="时间" :border="false">
              <van-dropdown-menu class="select-bar">
                <van-dropdown-item :title="tempFilter.beginTime" @open="handleBeginTimeClick" ref="beginTime">
                  
                </van-dropdown-item>
                <van-dropdown-item :title="tempFilter.endTime" @open="handleEndTimeClick" ref="endTime">
                  
                </van-dropdown-item>
              </van-dropdown-menu>
            </van-cell-group>
          </div>
        </a-scroll>

        <div class="order-filter-action">
          <van-button color="#108EE9" @click.native="handleConfirmFilter">确定</van-button>
        </div>
      </van-popup>
    </template>

    <template slot="blank">
      <div class="fault-action" @click="handleCreateFault"></div>
      <div class="problem-action" @click="handleCreateProblem"></div>


      <van-dialog
        v-model="beginTimeShow"
        title="开始时间"
        show-cancel-button
        @confirm="handleBeginTimeConfirm"
      >
        <van-datetime-picker
          v-model="beginTime"
          type="date"
          :show-toolbar="false"
        />
      </van-dialog>

      <van-dialog
        v-model="endTimeShow"
        title="结束时间"
        show-cancel-button
        @confirm="handleEndTimeConfirm"
      >
        <van-datetime-picker
          v-model="endTime"
          type="date"
          :show-toolbar="false"
        />
      </van-dialog>
    </template>
  </page>
</template>

<script>
import Page from "../../components/Page";
import AScroll from "../../components/Scroll";
import Item from "./Item";
import navigate from "../../utils/navigate.js";
import io from "../../api";
import db from "../../db";
import { statusEnum } from "../../utils/enum.js";
import native from '../../utils/native.js'
import moment from 'moment'

export default {
  components: {
    Page,
    Item,
    AScroll
  },
  data: () => ({
    headerHeightVW: 66 / 3.75,
    moreAction: false,
    moreTop: 0,
    header: null,
    filterLine: null,
    filterStation: null,
    filter: {
      key: "",
      line: "",
      station: "",
      lineObject: null,
      stationObject: null,
      // beginTime: moment()
      //   .startOf('years')
      //   .format('YYYY-MM-DD'),
      // endTime: moment()
      //   .endOf('years')
      //   .format('YYYY-MM-DD'),
      beginTime: '',
      endTime: '',
      pageIndex: 1,
      pageSize: 5,
      type: '1,2',
      status: 'assigned,pending_sign,waiting_repair,pending_submission,pending_item_review,pending_review,close,sold_item,hang',
      profession: '',
      level: ''
    },
    tempFilter: {},
    filterShow: false,
    tabActive: 0,
    receiveNum: 0,
    executingNum: 0,
    reviewNum: 0,
    suspendedNum: 0,
    statusEnum: statusEnum,
    addressOptions: [],
    columns: [],
    orderList: [],
    loading: true,
    allList: [],
    reasonOptions: [],
    typeOptions: [],
    levelOptions: JSON.parse(localStorage.getItem('level_options')),
    localList: [],
    professionOptions: JSON.parse(localStorage.getItem('profession_options')),
    beginTimeShow: false,
    beginTime: new Date(),
    endTimeShow: false,
    endTime: new Date(),
    FNScanner: null
  }),
  methods: {
    onReady(header, main) {
      this.header = header;

      console.log(JSON.stringify(header));
      console.log(JSON.stringify(main));

      this.headerHeightVW = ((this.header.h + 100) * 100) / this.header.w;
      this.moreTop = (this.header.h * 100) / this.header.w;

      this.loadStatistics();
      // this.loadOrderConfig()

      setInterval(() => {
        this.loadStatistics();
      }, 5000)

      db.getOrderIds().then(data => {
        // alert(JSON.stringify(data))
        this.localList = data

        this.syncLocalOrder()
      })

      this.onRefresh();
    },

    onMoreAction() {
      this.moreAction = true;
    },

    onShow() {
      this.loadStatistics();

      // this.onRefresh();
      if (localStorage.getItem('refresh_order_id') && this.tabActive != 2) {
        io.getOrderListId({id: localStorage.getItem('refresh_order_id')}).then(data => {
          
          const index = this.orderList.findIndex(item => item.id == data.detail.id)

          if (index >= 0) {
            this.$set(this.orderList, index, data.detail)
          }

          localStorage.setItem('refresh_order_id', '')

          // this.loadStatistics();
        }).catch(() => {
          localStorage.setItem('refresh_order_id', '')
        })
      }

      if (localStorage.getItem('address_select')) {
        const address = JSON.parse(localStorage.getItem('address_select'))
        this.tempFilter.lineObject = address[0]
        this.tempFilter.line = address[0].id
        this.tempFilter.stationObject = address[address.length - 1]
        this.tempFilter.station = this.tempFilter.stationObject.id

        localStorage.setItem('address_select', '')
      }
      

      this.syncLocalOrder()
    },

    onRefresh() {
      this.filter.pageIndex = 1;

      if (this.tabActive == 2) {
        db.getOrderList(this.filter).then(list => {
          this.orderList = list;
          this.filter.pageIndex++;
          this.$refs.page.finishRefresh();
          this.$refs.page.updateLoad();
          this.loading = false;
        });
      } else {
        io.getOrderList(this.filter)
          .then(data => {
            this.orderList = data.list;

            this.filter.pageIndex++;
            this.$refs.page.finishRefresh();
            this.loading = false;

            // this.syncLocalOrder(data.list)

            this.syncLocalOrder()
          })
          .catch(e => {
            this.$toast(e);
            this.$refs.page.finishRefresh();
            this.loading = false;
          });
      }
    },

    syncLocalOrder () {

      if (native.connection()) {
        io.getOrderSyncIds().then(data => {
          for (let i = 0; i < data.ids.length; i++) {
            let id = data.ids[i]

            if (!this.localList.includes(id)) {
              

              if (id.indexOf('maintenance_order') >=0 ) {
                io.getPlanDetail({id: id}).then(data => {
                  db.saveOrder({...data, type: 2})

                  // this.loadStatistics();
                })
              } else {
                io.getOrderDetail({id: id}).then(data => {
                  db.saveOrder({...data, type: 1})

                  // this.loadStatistics();
                })
              }
            }
          }

          for (let i = 0; i < this.localList.length; i++) {
            let localId = this.localList[i]

            if (!data.ids.includes(localId)) {
              db.removeOrder(localId).then(() => {
                // this.loadStatistics();
              })
            }
          }
        })
      }
    },

    onLoad() {
      if (this.tabActive == 2) {
        db.getOrderList(this.filter).then(list => {
          this.orderList = this.orderList.concat(list);
          this.filter.pageIndex++;
          if (data.list.length == 0) {
            this.$refs.page.endLoad();
          } else {
            this.$refs.page.updateLoad();
          }
          this.loading = false;
        });
      } else {
        io.getOrderList(this.filter)
          .then(data => {
            this.orderList = this.orderList.concat(data.list);
            this.filter.pageIndex++;
            if (data.list.length == 0) {
              this.$refs.page.endLoad();
            } else {
              this.$refs.page.updateLoad();
            }
            this.loading = false;

            // this.syncLocalOrder(data.list)
            // this.loadStatistics();

            this.syncLocalOrder()
          })
          .catch(e => {
            this.$toast(e);
            this.$refs.page.updateLoad();
            this.loading = false;
          });
      }
    },

    handleFilter() {
      this.tempFilter = { ...this.filter };

      this.filterShow = true;
    },

    handleScan () {
      if (!this.FNScanner) {
        this.FNScanner = api.require('FNScanner')
      }

      this.FNScanner.open({
        autorotation: true
      })
    },

    handleConfirmFilter() {
      this.filterShow = false;

      this.filter = {...this.filter, ...this.tempFilter}

      this.onRefresh();
    },

    handleDetail(item) {
      localStorage.setItem('refresh_order_id', item.id)
     
      if (item.type == 1) {
        navigate.open("orderdetail", { id: item.id });
      } else {
        navigate.open("plan_detail", { id: item.id })
      }
    },

    loadStatistics() {
      io.getOrderStatistics().then(data => {
        console.log(data);
        this.receiveNum = data.receive;
        // this.executingNum = data.executing
        this.reviewNum = data.review;
        this.suspendedNum = data.suspended;
      });

      db.getOrderCount().then(count => {
        this.executingNum = count;
      });
    },
    loadOrderConfig() {
      io.getOrderConfig().then(data => {
        this.addressOptions = data.address;

        localStorage.setItem("level_options", JSON.stringify(data.level));
        localStorage.setItem("reason_options", JSON.stringify(data.reason));
        localStorage.setItem("type_options", JSON.stringify(data.type));
        localStorage.setItem("line_options", JSON.stringify(data.address));
        localStorage.setItem(
          "profession_options",
          JSON.stringify(data.profession)
        );
        localStorage.setItem("source_options", JSON.stringify(data.source));
      });
    },

    handleToggleType(type) {
      let typeArray = this.tempFilter.type.split(",");

      if (this.tempFilter.type.indexOf(type) >= 0) {
        typeArray.splice(typeArray.findIndex(item => item == type), 1);
      } else {
        typeArray.push(type);
      }

      this.tempFilter.type = typeArray.join(",");
    },

    handleToggleProfession (profession) {
      if (this.tempFilter.profession != profession) {
        this.tempFilter.level = ''
      }
      this.tempFilter.profession = profession
    },

    handleToggleLevel (level) {
      this.tempFilter.level = level
    },

    handleToggleStatus(key) {
      let typeArray = this.tempFilter.status.split(",");

      if (this.tempFilter.status.indexOf(key) >= 0) {
        typeArray.splice(typeArray.findIndex(item => item == key), 1);
      } else {
        typeArray.push(key);
      }

      this.tempFilter.status = typeArray.join(",");
    },

    handleCreateFault() {
      this.moreAction = false;
      navigate.open("createFault");
    },

    handleCreateProblem() {
      this.moreAction = false;
      navigate.open("createProblem");
    },

    handleSelectLine() {
      navigate.open('address')
    },

    handleBeginTimeClick () {
      this.beginTime = new Date(this.tempFilter.beginTime)
      this.beginTimeShow = true
    },

    handleBeginTimeConfirm (value) {
      console.log(this.beginTime)
      // this.$refs.beginTime.toggle()
      this.tempFilter.beginTime = moment(this.beginTime).format('YYYY-MM-DD')
      this.beginTimeShow = false
    },

    handleEndTimeClick () {
      this.endTime = new Date(this.tempFilter.endTime)
      this.endTimeShow = true
    },

    handleEndTimeConfirm (value) {
      console.log(this.endTime)
      // this.$refs.beginTime.toggle()
      this.tempFilter.endTime = moment(this.endTime).format('YYYY-MM-DD')
      this.endTimeShow = false
    },

    handleToMe () {
      navigate.open('me')
    }
  },
  watch: {
    tabActive(val) {
      this.loading = true;
      if (val == 0) {
        this.filter.status =
          "assigned,pending_sign,waiting_repair,pending_submission,pending_item_review,pending_review,close,sold_item,hang";
        this.filter.pageIndex = 1;
      }
      if (val == 1) {
        this.filter.status = "assigned";
        this.filter.pageIndex = 1;
      }
      if (val == 3) {
        this.filter.status = "pending_item_review,pending_review";
        this.filter.pageIndex = 1;
      }
      if (val == 4) {
        this.filter.status = "hang";
        this.filter.pageIndex = 1;
      }
      if (val == 5) {
        this.filter.status = "close,sold_item";
        this.filter.pageIndex = 1;
      }

      this.$nextTick(() => {
        this.onRefresh();
      });
    },
    'filter.key': function (val) {
      this.onRefresh()
    }
  }
};
</script>

<style lang="scss">
.header-more {
  right: 10px;
  width: 100px;
  left: auto !important;
  transform: none !important;
  transition: none;
  padding: 10px 0;
}
.order-tabs {
  height: 44px;
  overflow: hidden;

  .van-tabs__line {
    background-color: #1b82d2;
  }

  .order-tab-tag {
    position: absolute;
    font-size: 10px;
    // width: 16px;
    min-width: 16px;
    height: 16px;
    background: #ff6166;
    border-radius: 8px;
    line-height: 16px;
    text-align: center;
    color: #fff;
    top: 5px;
  }
}

.order-page {
  .order-list {
    padding: 16px;
  }

  .page-loading {
    margin: 0 auto;
    top: 20px;
    text-align: center;
  }

  .order-filter-popup {
    width: 300px;
    height: 100%;

    .order-filter-action {
      padding: 20px 16px;
      display: flex;
      justify-content: space-between;

      button {
        display: block;
        width: 100%;
      }
    }

    .order-filter-scroll {
      height: calc(100% - 100px);
    }

    .order-filter-content {
      padding: 20px 0;

      .xbzd {
        width: 90%;
        height: 30px;
        margin-left: 16px;
        line-height: 30px;
      }
    }

    .filter-options {
      padding: 0 16px;
    }

    .filter-options-item {
      background: rgba(51, 51, 51, 0.05);
      height: 36px;
      line-height: 36px;
      color: #000;
      border-radius: 2px;
      text-align: center;
      margin-bottom: 10px;
      position: relative;

      &.is-active {
        background: rgba(198, 230, 253, 0.52);
        color: rgba(16, 142, 233, 1);

        &::after {
          display: block;
          content: "";
          background: url("../../assets/images/order/active.png") no-repeat;
          background-size: 100% 100%;
          width: 20px;
          height: 18px;
          position: absolute;
          bottom: 0;
          right: 0;
        }
      }
    }
  }

  .filter-options-time {
    .van-picker__toolbar {
      display: none;
    }
  }
}

.list-enter-active,
.list-leave-active {
  transition: all 1s;
}
.list-enter, .list-leave-to
/* .list-leave-active for below version 2.1.8 */ {
  opacity: 0;
  transition: all 0s;
  transform: translateY(30px);
}
</style>
